@import url(//db.onlinewebfonts.com/c/b2e75c336df191233b3096682c9ee6be?family=PixelarRegularW01-Regular);
*{list-style-type:none;margin:0;font-family:'PixelarRegularW01-Regular';box-sizing:border-box;text-align:center}

.card{border:1px solid grey;border-radius:8px;width:100%;height:190px;margin:10px;padding:10px;display:flex;flex-direction:column;justify-content:space-evenly;background-color:rgba(255,255,255,0.5)}.card .name{text-transform:capitalize;font-size:24px}.card .types_container{border-top:1px solid grey;display:flex;padding:0}.card .types_container .types_item{background-color:grey;color:white;font-size:14px;border-radius:15px;margin:5px;padding:1px 8px 2px 8px}.card .types_container .POISON{background-color:#8e1380}.card .types_container .GRASS{background-color:#1bc45e}.card .types_container .WATER{background-color:#00d0ff}.card .types_container .FIRE{background-color:#fe6b10}.card .types_container .FLYING{background-color:#8b93ee}.card .types_container .BUG{background-color:#b3c920}

.h1{font-size:45px;margin-top:30px}.pokeList{max-width:820px;max-height:70vh;overflow-y:auto;overflow-x:hidden;display:flex;flex-wrap:wrap;justify-content:space-evenly;align-items:center;padding:0;margin:20px 40px 100px 40px}.pokeList .pokeList_item{width:150px}

.App{background-image:url(../../static/media/wallpaper.5f2047d7.jpg);background-attachment:fixed;background-position:center;background-size:cover;height:100vh;display:flex;flex-direction:column;justify-content:space-between;align-items:center}.App .top{height:70vh}.App .wild_pkm3{width:100vw;height:20vh}.App .wild_pkm3 .psyduck_img{position:absolute;left:0;bottom:5%;height:250px;width:250px;transform:scaleX(-1)}@media all and (max-height: 650px){.App .wild_pkm3 .psyduck_img{display:none}}.hidden{display:none}

